import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';

var game_info = {
  next_state : null,
  board_states : [null, null, null, null, null, null, null, null, null],
  winner : null,
};

const checkGanador = function(arr){
  // horizontal
  if(arr[0] === arr[1] && arr[1] === arr[2]){
    console.log('Ganador: '+arr[0]);
  }
  if(arr[3] === arr[4] && arr[4] === arr[5]){
    console.log('Ganador: '+arr[3]);
  }
  if(arr[6] === arr[7] && arr[7] === arr[8]){
    console.log('Ganador: '+arr[6]);
  }

  // vertical
  if(arr[0] === arr[3] && arr[3] === arr[6]){
    console.log('Ganador: '+arr[0]);
  }
  if(arr[1] === arr[4] && arr[4] === arr[7]){
    console.log('Ganador: '+arr[1]);
  }
  if(arr[2] === arr[5] && arr[5] === arr[8]){
    console.log('Ganador: '+arr[2]);
  }

  // cross
  if(arr[0] === arr[4] && arr[4] === arr[8]){
    console.log('Ganador: '+arr[0]);
  }
  if(arr[2] === arr[4] && arr[4] === arr[6]){
    console.log('Ganador: '+arr[2]);
  }
}


class Square extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      value : null,
    };
    let r = Math.round(Math.random());
    if(r === 0){
      game_info.next_state = 'O';
    }
    else{
      game_info.next_state = 'X';
    }
  }

  render() {
    return (
      <button
        className="square"
        onClick = {() => {
          if(this.state.value === null){
            this.setState({value : game_info.next_state});
            game_info.board_states[this.props.value] = game_info.next_state;
            // Changing state
            if(game_info.next_state === 'X'){
              game_info.next_state = 'O';
            }
            else{
              game_info.next_state = 'X';
            }
            console.log(game_info.board_states);
            checkGanador(game_info.board_states);
          }
        }}
      >
        {this.state.value}
      </button>
    );
  }
}


class Board extends React.Component {
  renderSquare(i) {
    return <Square value={i}/>;
  }

  render() {
    const status = 'Next player: X';

    return (
      <div>
        <div className="status">{status}</div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}


class Game extends React.Component {
  render() {
    return (
      <div className="container">
        <div className="game">
          <div className="game-board">
            <Board />
          </div>
          <div className="game-info">
            <div>{/* status */}</div>
            <ol>{/* TODO */}</ol>
          </div>
        </div>
      </div>
    );
  }
}

// ========================================

ReactDOM.render(
  <Game />,
  document.getElementById('root')
);